.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  padding:0;
  margin:0;
  box-sizing:border-box
}
body{
  background-color: grey;
  height:40%;
  width: 500px;
  position: absolute;
  margin: auto;
  top: 70px;
  left: 0%;
  right:0%;
  padding: 0%;
  border-end-end-radius: 6px;
}
#lgmtodolist{
  position:relative;
  padding: 30px 15px;
  background:darkcyan;
  box-shadow: 0 15px 30px black ;
}
#lgmtodolist input{
  border-radius: 10px;
  max-width: 400px;
  height: 40px;
  font-family: 'Times New Roman', Times, serif;
  color: black;
  font-size: 20px;
  border: 20px black;
  padding: 20px;
  width: 84%;
  position: relative;
  box-shadow: 4px black;
 
}
#lgmtodolist input:focus{
  outline: auto;
  border-radius: 2px;
  border-color:black;
}
#lgmtodolist button{
  position: relative;
  float:right;
  width:60px;
  height: 40px;
  border-radius: 3px;
  font-size: 15px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  background: whitesmoke;
  /* color:whitesmoke; */
  cursor: pointer;
}
main{
  flex: 5 4 0%;
  padding:1rem 2rem;
  max-width:500px;
  width:200px;
  margin:0 auto;

}
.task-list{
  padding:4 rem;

}
.task-list h2{
  font-size:1.5rem;
  font-weight:300;
  color:var(--black);
  margin-bottom: 1rem;

}
ul{
  list-style: none !important;
}
